<svelte:head>
	<link href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css" rel="stylesheet" integrity="sha384-mhpbKVUOPCSocLzx2ElRISIORFRwr1ZbO9bAlowgM5kO7hnpRBe+brVj8NNPUiFs" crossorigin="anonymous">
<script src="https://www.gstatic.com/firebasejs/7.14.6/firebase-remote-config.js"></script>
</svelte:head>
<script context="module">
import gradstop from 'gradstop';

export function getRandomColor() {
let letters = '0123456789ABCDEF';
let color = '#';
for (var i = 0; i < 6; i++) {
color += letters[Math.floor(Math.random() * 16)];
}
return color;
}

export function randDarkColor() {
    var lum = -0.25;
    var hex = String('#' + Math.random().toString(16).slice(2, 8).toUpperCase()).replace(/[^0-9a-f]/gi, '');
    if (hex.length < 6) {
        hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
    }
    var rgb = "#",
        c, i;
    for (i = 0; i < 3; i++) {
        c = parseInt(hex.substr(i * 2, 2), 16);
        c = Math.round(Math.min(Math.max(0, c + (c * lum)), 255)).toString(16);
        rgb += ("00" + c).substr(c.length);
    }
    return rgb;
}

// The code here is supposed to generate a palette of colors of similar harmony or theme, return an array
export function colorPalette(){
    let gradient = gradstop({
    stops: 6,
    inputFormat: 'hex',
    colorArray: [randDarkColor(), '#CCCCCC']
});
 
//console.log(gradient);
return gradient;
}

</script>