<svelte:head>
	<link href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css" rel="stylesheet" integrity="sha384-mhpbKVUOPCSocLzx2ElRISIORFRwr1ZbO9bAlowgM5kO7hnpRBe+brVj8NNPUiFs" crossorigin="anonymous">
<script src="https://www.gstatic.com/firebasejs/7.14.6/firebase-remote-config.js"></script>
</svelte:head>
<script context="module">
  import * as blobs2 from "blobs/v2";

  //This function generates the code for blob from the blobs/v2 package
  export function BlobGenerator() {
    let valueArray = [150,200,256,60,172,320]
    let randomElement = Math.floor(Math.random() * 200) + 100 ;
    //valueArray[Math.floor(Math.random() * valueArray.length)];

    const svgPath = blobs2.svgPath({
    seed: Math.random(),
    extraPoints: 6,
    randomness: 2,
    size: randomElement
  });

  return svgPath;
  }

 //this function calls the above func. 5 times and returns an array of the code of blobs generated
  export function FiveBlobs(){
    let i = 5;
    let blobArray = [5];
    
    for (i=0;i<5;i++) 
    {
      blobArray[i] = BlobGenerator();
    }

    return blobArray;
  }
</script>