<svelte:head>
	<link href="https://stackpath.bootstrapcdn.com/bootswatch/4.5.0/flatly/bootstrap.min.css" rel="stylesheet" integrity="sha384-mhpbKVUOPCSocLzx2ElRISIORFRwr1ZbO9bAlowgM5kO7hnpRBe+brVj8NNPUiFs" crossorigin="anonymous">
</svelte:head>
<script context="module">
  import {  Button, Col, Container, Row, Form, FormGroup, FormText, Input, CustomInput, Label } from 'sveltestrap';
  import {randDarkColor} from "./Color.svelte";

	export function remixBackgroundColor() {
		//let bgColor= '#'+(0x1000000+(Math.random())*0xffffff).toString(16).substr(1,6);
		document.getElementById('userBGcolor').value = randDarkColor();
	}

	export function remixColorCombo() {
		let bgColor= getRandomColor();
		//'#'+(0x1000000+(Math.random())*0xffffff).toString(16).substr(1,6);
		let txtColor= "#"+((1<<24)*Math.random()|0).toString(16);
		
		let colorArr = [bgColor,txtColor];

		return colorArr;
	}

	// THis function generates LIGHT colors only
	function getRandomColor() {
                var letters = 'BCDEF'.split('');
                var color = '#';
                for (var i = 0; i < 6; i++ ) {
                    color += letters[Math.floor(Math.random() * letters.length)];
                }
                return color;
            }
	
	export function generateRandomTextColor(){
		let color = '#'+(0x1000000+(Math.random())*0xffffff).toString(16).substr(1,6);
		return color;
	}

	export function generateRandomFontStyle(){
		let arrayOfFontStyles = ["Lucida Console","Trebuchet MS",'Tahoma','Monaco','Verdana', 'Calibri', 'Sans-serif','Cursive','Fantasy','Helvetica','Arial']
		let randomStyle = arrayOfFontStyles[Math.floor(Math.random() * arrayOfFontStyles.length)]
		return randomStyle;
	}
</script>

